/* src/components/ContactForm.css */
.contact-form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh; /* Adjust this value as needed */
  background-color: #f4f4f4; /* Optional: Add background color */
}

.contact-form {
  display: flex;
  flex-direction: column;
  max-width: 400px; /* Control the width of the form */
  width: 100%; /* Ensure the form is responsive */
  padding: 1rem;
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Optional: Add some shadow */
}

.contact-form label {
  margin: 0.5rem 0;
}

.contact-form input,
.contact-form textarea {
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.contact-form button {
  padding: 0.5rem;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.contact-form button:disabled {
  background-color: #ccc;
}
