   .home {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: linear-gradient(to right, #012075, #7bfe7b);
    color: white;
    text-align: center;
  }
  
  .home .content {
    max-width: 600px;
  }
  
  .home h1 {
    font-size: 3em;
    margin-bottom: 20px;
  }
  
  .home p {
    font-size: 1.2em;
    margin-bottom: 30px;
  }
  
  .home .btn {
    padding: 15px 30px;
    background-color: white;
    color: #ff7e5f;
    text-decoration: none;
    border-radius: 25px;
    transition: background-color 0.3s, color 0.3s;
  }
  
  .home .btn:hover {
    background-color: #ff7e5f;
    color: white;
  }
  



 @media (max-width: 960px) {
  .home {
    padding: 20px;
  }

  .home h1 {
    font-size: 2.5em;
  }

  .home p {
    font-size: 1em;
  }

  .home .btn {
    padding: 12px 25px;
  }
}

@media (max-width: 600px) {
  .home h1 {
    font-size: 2em;
  }

  .home p {
    font-size: 0.9em;
  }

  .home .btn {
    padding: 10px 20px;
    font-size: 0.9em;
  }
} 